

.custom-autocomplete {
    .custom-autocomplete__control:hover {
        border-color: #000000;
        border-width: 1px;
        box-shadow: none;
    }
    .custom-autocomplete__control--is-focused {
        border-color: #3AB77D;
        border-width: 2px;
        box-shadow: none;
    }
    .custom-autocomplete__control--is-focused:hover {
        border-color: #3AB77D;
        border-width: 2px;
        box-shadow: none;
    }
}